%glow-animation {
    animation: glow 1s ease-in-out infinite alternate;

    @keyframes glow {
        0% {
            text-shadow: 0 0 10px #ffffff,
                         0 0 20px #ffffff,
                         0 0 30px #e60073,
                         0 0 40px #e60073,
                         0 0 50px #e60073,
                         0 0 60px #e60073,
                         0 0 70px #e60073;
        }
        100% {
            text-shadow: 0 0 20px #ffffff,
                         0 0 30px #ff4da6,
                         0 0 40px #ff4da6,
                         0 0 50px #ff4da6,
                         0 0 60px #ff4da6,
                         0 0 70px #ff4da6,
                         0 0 80px #ff4da6;
        }
    }
}
