@import 'src/styles/variables';

.container {
    display: flex;
    flex-direction: column;
    gap: 20px;

    width: 270px;
    height: 100vh;

    padding: 20px 40px 20px 20px;
    border-radius: 0 18px 18px 0;

    background-color: $white;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 5%);
}
