@import 'src/styles/variables';

.container {
    display: grid;
}

.currencyItem {
    display: inline-block;

    color: $grey-200;
    text-align: center;

    font-size: $font-28;
    font-weight: 500;

    /* fix the border overlay */
    margin: 0 -0.025rem;
    padding: 12px 10px;

    cursor: pointer;
    border: 2px solid $grey-100;

    &:has(input:checked) {
        color: $white;
        background-color: $purple;
        border-color: $purple;
    }

    &:first-child {
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
    }

    &:last-child {
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
    }

    input {
        appearance: none;
    }
}
