.grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, 200px);
    justify-content: space-between;
    grid-gap: 20px;

    width: 100%;
}

.imageContainer {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 200px;
    height: 200px;

    overflow: hidden;
    border-radius: 6px;
}

.image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
