@import 'src/styles/variables';
@import 'src/styles/animations';

$image-horizontal-border: 9%;
$image-vertical-border: 10.2%;

.container {
    display: flex;
    justify-content: center;
    align-items: center;

    height: calc(100vh - 40px);
    background: linear-gradient(to bottom, $purple-100, $blue-300);
}

.imageContainer {
    position: relative;
}

.imageContainer,
.device {
    height: 90vh;
    max-width: 100%;
    overflow: hidden;
}

.startButton {
    position: absolute;
    right: 2%;
    top: 50%;
    transform: translateY(-50%);

    width: 5.6%;
    background: transparent;
    border-radius: 50%;
    overflow: hidden;
}

.buttonIcon {
    width: 100%;
}

.clickMeArrow,
.clickMeLabel,
.greetings {
    position: absolute;
    z-index: 10;
}

.clickMeArrow {
    top: 50%;
    right: 12%;

    width: 14%;
    filter: invert(100%);
    transform: rotate(270deg) translateX(100%);
}

.clickMeLabel {
    top: 32%;
    right: 30%;

    color: $white;
    font-size: $font-36;
    @extend %glow-animation;
}

.greetings {
    right: 32%;
    bottom: 8%;
    width: 32%;
}

.quiz {
    position: absolute;
    top: $image-vertical-border;
    left: $image-horizontal-border;

    width: calc(100% - $image-horizontal-border * 2);
    height: calc(100% - $image-vertical-border * 2);

    padding: 30px;
    background: linear-gradient(to right, $blue-100, $blue-400);
}
